<template>
	<view>
		<view class="u-text-center color-000000 bold">
			<u-gap height="90"></u-gap>
			<view class="u-font-30">添加银行卡</view>
			<u-gap height="15"></u-gap>
			<view class="u-font-24">请绑定持卡人本人的银行卡</view>
		</view>
		<view class="u-padding-50">
			<u-line color="#999999"></u-line>
			<view class="u-flex color-000000 bold u-font-24">
				<view class="width-160">持卡人</view>
				<view class="u-flex-1">
					<u-input v-model="name" :custom-style="customStyle" placeholder="请输入姓名" :placeholder-style="placeholderStyle"/>
				</view>
			</view>
			<u-line color="#999999"></u-line>
			<view class="u-flex color-000000 bold u-font-24">
				<view class="width-160">银行卡号</view>
				<view class="u-flex-1">
					<u-input v-model="backAccount" :custom-style="customStyle" placeholder="请输入本人银行卡号" :placeholder-style="placeholderStyle"/>
				</view>
			</view>
			<u-line color="#999999"></u-line>
			<view class="u-flex color-000000 u-font-24">
				<view class="width-160 bold">开户行</view>
				<view class="u-flex-1 line-height-70" @click="show=true">
					<text :class="backName?'color-000000':'color-999999'">{{backName?backName:"请选择开户行"}}</text>
				</view>
			</view>
			<u-line color="#999999"></u-line>
		</view>
		<view class="btn-wrap">
			<u-button type="primary" shape="circle" @click="handleClick">确认添加</u-button>
		</view>
		<u-select v-model="show" :list="list" label-name="backName" value-name="backId" @confirm="confirm"></u-select>
	</view>
</template>

<script>
	import * as config from "../../../common/config.js"
	export default {
		data() {
			return {
				name: '',
				backAccount: '',
				backName: '',
				backCode: '',
				customStyle : {
					fontWeight: "normal",
					fontSize: "24rpx"
				},
				placeholderStyle : {
					color: "#999999",
					fontSize: "24rpx"
				},
				show:false,
				list:[]
			}
		},
		onLoad() {
			this.list = config.backList
		},
		methods: {
			confirm(e) {
				this.backName = e[0].label
				this.backCode = e[0].value
			},
			handleClick() {
				this.$u.api.addBankAccount({
					name: this.name,
					backAccount: this.backAccount,
					backName: this.backName,
					backCode: this.backCode
				}).then(res => {
					uni.showToast({
						title: "绑定成功",
						icon: "none",
						duration: 2000,
						mask: true,
						success: function() {
							setTimeout(() => {
								uni.navigateBack()
							}, 2000)
						}
					})
				})
			}
		}
	}
</script>

<style lang="scss">
	.width-160 {
		width: 160rpx;
	}
	.line-height-70 {
		line-height: 70rpx;
	}
	.btn-wrap {
		width: 80%;
		margin: 500rpx auto 0;
	}
</style>
